import Button from 'quark-ui/button';
import Dropdown from '../Dropdown';

const { Menu } = Dropdown;
const { Item } = Menu;

const DropdownDemo = () => {
  const menu = (
    <Menu>
      <Item>
        <a href="https://www.ehuodi.com"></a>
      </Item>
      <Item>
        <a href="http://www.lujing56.cn/"></a>
      </Item>
      <Item>
        <a href="https://ecargo.ehuodi.com/"></a>
      </Item>
    </Menu>
  );
  return (
    <div className="markdown-block">
      <h3></h3>
      <Dropdown overlay={menu}>
        <Button></Button>
      </Dropdown>
      <h3>Dropdown</h3>
      <Dropdown.Button type="secondary" overlay={menu} trigger={'click'}>
        
      </Dropdown.Button>
    </div>
  );
};

export default DropdownDemo;



