import { Component } from 'react';
import Spin from '../Spin';
import Alert from '../../alert';
import Button from '../../button';
import style from './index.css';

export default class SpinDemo extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isShow1: true,
      isShow2: false,
    };
  }

  swichHandle1 = () => {
    this.setState({
      isShow1: !this.state.isShow1
    })
  }

  swichHandle2 = () => {
    this.setState({
      isShow2: !this.state.isShow2
    })
  }

  render() {
    return (
      <div className="markdown-block">
        <h3></h3>
        <Spin />
        <h3></h3>
        <table>
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>
                  <Spin size="small" />
              </td>
              <td>
                  <Spin size="default" />
              </td>
              <td>
                  <Spin size="large" />
              </td>
            </tr>
          </tbody>
        </table>
        <h3></h3>
        <table>
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>
                  <Spin size="small" tip="loading..." />
              </td>
              <td>
                  <Spin size="default" tip="loading..." />
              </td>
              <td>
                  <Spin size="large" tip="loading..." />
              </td>
            </tr>
          </tbody>
        </table>
        <h3></h3>
        <div className={style.example1}>
          <Spin />
        </div>
        <div className={style.example1}>
          <Spin tip="loading..." />
        </div>
        <h3></h3>
        <div>
          <Spin spinning={this.state.isShow1}>
            <Alert
              type="info"
              message=""
              description={`
              
              
              
              `}
            />
          </Spin>
          <p>
            <Button type="primary" onClick={this.swichHandle1}>/</Button>
          </p>
        </div>
        <h3></h3>
        <div>
          <Spin spinning={this.state.isShow2} delay={600}>
            <Alert
              type="info"
              message=""
              description={`
              
              
              
              
              `}
            />
          </Spin>
          <p>
            <Button type="primary" onClick={this.swichHandle2}>/</Button>
          </p>
        </div>
      </div>
    );
  }
}



