import { Component } from 'react';
import Button from '../../button/Button';
import Select from '../Select';
import OptGroup from '../OptGroup';
import Option from '../Option';
import querystring from 'querystring';
import jsonp from 'fetch-jsonp';

class SelectDemo1 extends Component {
  constructor() {
    super();
    this.state = {
      disabled: false,
      value: null,
      text: null,
    };
  }

  onChange = ({ text, value }) => {
    this.setState({
      value,
      text,
    });
  }

  setDisabled = () => {
    this.setState({
      disabled: !this.state.disabled,
    });
  }

  setSelect = () => {
    this.setState({
      value: 'B',
    });
  }

  render() {
    const { value, disabled } = this.state;

    return (
      <div className="markdown-block">
        <Button onClick={this.setDisabled}>{ disabled ? '' : ''}</Button> &nbsp;&nbsp;
        <Button onClick={this.setSelect}>{ 'BB' }</Button>
        <h3></h3>
        <p />
        <Select style={{ width: 250 }} disabled={disabled} defaultValue="C" value={value} onChange={this.onChange} placeholder={""}>
          <Option value="A">AA</Option>
          <Option value="B">BB</Option>
          <Option value="C">CC</Option>
          <Option value="D">DD</Option>
          <Option value="E">EE</Option>
          <Option value="F">FF</Option>
          <Option value="G">GG</Option>
        </Select>
        <span>:{`${this.state.value},${this.state.text}`}</span>

        <h3></h3>
        <p />
        <Select style={{ width: 250 }} disabled={disabled} defaultValue="C" onChange={this.onChange} placeholder={""}>
          <Option value="A">AA</Option>
          <Option value="B">BB</Option>
          <Option value="C">CC</Option>
          <Option value="D">DD</Option>
          <Option value="E">EE</Option>
          <Option value="F">FF</Option>
          <Option value="G">GG</Option>
        </Select>
        <span>:{`${this.state.value},${this.state.text}`}</span>
      </div>
    );
  }
}


