class SelectDemo3 extends Component {
  constructor() {
    super();
    this.state = {
      disabled: false,
      value: null,
      text: null,
      searchData: [],
    };
  }

  setDisabled = () => {
    this.setState({
      disabled: !this.state.disabled,
    });
  }

  setDefaultValue = () => {
    this.setState({
      value: 'CPU',
      text: 'CPU',
    });
  }

  onSearch = (value) => {
    const str = querystring.encode({
      code: 'utf-8',
      q: value,
    });
    jsonp(`https://suggest.taobao.com/sug?${str}`)
      .then(response => response.json())
      .then((d) => {
        const result = d.result;
        const data = [];
        result.forEach((r) => {
          data.push({
            value: r[0],
            text: r[0],
          });
        });
        this.setState({
          searchData: data,
        });
      });
  }

  onChange = ({ value, text }) => {
    this.setState({
      value,
      text,
    });
  }

  render() {
    return (
      <div className="markdown-block">
        <Button onClick={this.setDisabled}>{ this.state.disabled ? '' : ''}</Button> &nbsp;&nbsp;
        <Button onClick={this.setDefaultValue}></Button>
        <h3></h3>
        <p />
        <Select
          style={{ width: 250 }}
          disabled={this.state.disabled}
          value={this.state.value}
          text={this.state.text}
          type="combobox"
          onSearch={this.onSearch}
          onCancelChange={this.onCancelChange}
          onChange={this.onChange}
          placeholder=""
        >
          {
            this.state.searchData.map(d => <Option key={d.value} value={d.value}>{d.text}</Option>)
          }
        </Select>

        <span>:{`${this.state.value},${this.state.text}`}</span>
      </div>
    );
  }
}

class SelectDemo4 extends Component {
  constructor() {
    super();
    this.state = {
      value: null,
      text: null,
      disabled: false,
      province: null,
      city: null,
    };
  }

  setDisabled = () => {
    this.setState({
      disabled: !this.state.disabled,
    });
  }

  onChangeProvince = ({ value, text }) => {
    this.setState({
      province: value,
      provinceText: text,
      city: null,
      cityText: null,
    });
  }
  onChangeCity = ({ value, text }) => {
    this.setState({
      city: value,
      cityText: text,
    });
  }

  getCitysByProvince = (province) => {
    switch (province) {
      case '1':
        return [{ value: '11', text: '' },
          { value: '12', text: '' },
          { value: '13', text: '' }];
      case '2':
        return [{ value: '21', text: '' },
          { value: '22', text: '' },
          { value: '23', text: '' }];
      case '3':
        return [{ value: '31', text: '' },
          { value: '32', text: '' },
          { value: '33', text: '' }];
      default:
        return [];
    }
  }

  render() {
    const citys = this.getCitysByProvince(this.state.province).map((v, i) => <Option value={v.value} key={i}>{v.text}</Option>);

    return (
      <div className="markdown-block">
        <Button onClick={this.setDisabled}>{ this.state.disabled ? '' : ''}</Button>
        <h3></h3>
        <p />
        
        <Select style={{ width: 250 }} disabled={this.state.disabled} value={this.state.province} onChange={this.onChangeProvince}>
          <Option value="1"></Option>
          <Option value="2"></Option>
          <Option value="3"></Option>
        </Select>
        
        <Select style={{ width: 250 }} disabled={this.state.disabled} value={this.state.city} onChange={this.onChangeCity}>
          {citys}
        </Select>
        <span>:{`${this.state.province}-${this.state.provinceText},${this.state.city}-${this.state.cityText}`}</span>
      </div>
    );
  }
}

export default class SelectDemo extends Component {
  render() {
    return (
      <div className="markdown-block">
        <SelectDemo1 />
        <br /><br />
        <SelectDemo2 />
        <br /><br />
        <SelectDemo3 />
        <br /><br />
        <SelectDemo4 />
      </div>
    );
  }
}



