import { Component } from 'react';
import Button from '../../button';
import Steps from '../Steps';

const Step = Steps.Step;

export default class StepDemo extends Component {
  constructor(props) {
    super(props);
    this.state = {
      current: 0,
    };
    this.handleNext = this.handleNext.bind(this);
    this.handlePrev = this.handlePrev.bind(this);
  }

  handleNext() {
    this.setState((preState) => {
      if (preState.current < 3) {
        return { current: preState.current + 1 };
      }
      return { current: 3 };
    });
  }

  handlePrev() {
    this.setState((preState) => {
      if (preState.current > 0) {
        return { current: preState.current - 1 };
      }
      return { current: 0 };
    });
  }

  render() {
    return (
      <div className="markdown-block">
        <h3><Button disabled={this.state.current <= 0} onClick={this.handlePrev}></Button>&nbsp;
          <Button disabled={this.state.current >= 3} onClick={this.handleNext}></Button></h3>
        <h3></h3>
        <Steps current={this.state.current} style={{ marginBottom: 10 }}>
          <Step title="1" />
          <Step title="2" />
          <Step title="3" />
          <Step title="4" />
        </Steps>
        <Steps current={this.state.current} isFinishIcon style={{ marginBottom: 10 }}>
          <Step title="" />
          <Step title="" />
          <Step title="" />
          <Step title="" />
        </Steps>
        <Steps current={this.state.current} style={{ marginBottom: 10 }}>
          <Step title="1" description="" />
          <Step title="2" description="" />
          <Step title="3" description="" />
          <Step title="4" description="" />
        </Steps>

        <h3></h3>
        <div style={{ width: 400, display: 'inline-block' }}>
          <Steps current={this.state.current} direction="vertical">
            <Step title="1" description="" />
            <Step title="2" description="" />
            <Step title="3" description="" />
            <Step title="4" />
          </Steps>
        </div>
        <div style={{ display: 'inline-block' }}>
          <Steps current={this.state.current} size={'small'} direction="vertical" isFinishIcon>
            <Step title="" description="" />
            <Step title="" description="" />
            <Step title="" description="" />
            <Step title="" description="" />
          </Steps>
        </div>
      </div>
    );
  }

}



