import React, { Component } from 'react';
import Upload from '../Upload';
import Icon from '../../icon/Icon';
import Button from '../../button/Button';
import message from '../../message/index';
import styles from './index.css';

class UploadDemo1 extends Component {
  constructor(props) {
    super(props);
    this.state = {
    };
  }
  render() {
    const props = {
      name: 'file',
      action: 'https://jsonplaceholder.typicode.com/posts/',
      headers: {
        authorization: 'authorization-text',
      },
      multiple: true,
      disabled: false,
      onResponse(response) {
        response = { result: 'success', msg: '', url: 'https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png' };
        if (response.result === 'success') {
          return {
            success: true,
            message: '',
            url: response.url,
          };
        }

        return {
          success: false,
          message: response.msg,
        };
      },
      onChange(info) {
        if (info.file.status !== 'uploading') {
          // console.log(info.file, info.fileList);
        }
        if (info.file.status === 'done') {
          message.success(`${info.file.name} .`);
        } else if (info.file.status === 'error') {
          message.error(`${info.file.name} `);
        }
      },
    };

    return (
      <div className="markdown-block">
        <h3>1</h3>
        <Upload {...props}>
          <Button size="small" type="secondary" disabled={props.disabled}>
            <Icon size={12} name="upload" /> 
          </Button>
        </Upload>
      </div>
    );
  }
}

