class UploadDemo3 extends Component {
  constructor(props) {
    super(props);
    this.state = {
      fileList: [{
        uid: -1,
        name: 'xxx.png',
        status: 'done',
        url: 'https://www.ehuodi.com/module/index/img/index2/line2_bg.png',
      }],
    };
  }

  render() {
    const I = this;
    const props = {
      action: '//jsonplaceholder.typicode.com/posts/',
      disabled: false,
      onResponse(response) {
        response = { result: 'success', msg: '', url: 'https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png' };
        if (response.result === 'success') {
          return {
            success: true,
            message: '',
            url: response.url, // 
          };
        }

        return {
          success: false,
          message: response.msg,
        };
      },
      onChange(info) {
        let fileList = info.fileList;

        // 1
        fileList = fileList.slice(-1);

        // 
        fileList = fileList.map((file) => {
          if (file.response) {
            file.url = file.response.url;
          }
          return file;
        });

        // // 
        // fileList = fileList.filter((file) => {
        //   if (file.response) {
        //     return file.status === 'done';
        //   }
        //   return true;
        // });

        I.setState({ fileList });
      },
    };
    return (
      <div className="markdown-block">
        <h3>3 fileList </h3>
        <p>1) </p>
        <p>2) </p>
        <p>3) </p>
        <Upload {...props} fileList={this.state.fileList}>
          <Button size="small" type="secondary" disabled={props.disabled}>
            <Icon size={12} name="upload" /> 
          </Button>
        </Upload>
      </div>
    );
  }
}

class UploadDemo4 extends Component {
  constructor(props) {
    super(props);
    this.state = {
      fileList: [{
        uid: -1,
        name: 'xxx.png',
        status: 'done',
        url: 'https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png',
      }],
    };
  }
  // 
  handlePreview = (file) => {
    window.open(file.url);
  }

  handleChange = (info) => {
    this.setState({ fileList: info.fileList });
  }
  beforeUpload(file) {
    const isJPG = file.type === 'image/png';
    if (!isJPG) {
      message.error('.png!');
    }
    const isLt2M = file.size < 1024 * 1000;
    if (!isLt2M) {
      message.error('1000KB!');
    }
    return isJPG && isLt2M;
  }
  render() {
    const props = {
      action: '//jsonplaceholder.typicode.com/posts/',
      disabled: false,
      listType: 'picture-card',
      fileList: this.state.fileList,
      onPreview: this.handlePreview,
      onChange: this.handleChange,
      beforeUpload: this.beforeUpload,
      onResponse(response) {
        response = { result: 'success', msg: '', url: 'https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png' }; // mock
        if (response.result === 'success') {
          return {
            success: true,
            message: '',
            url: response.url, // 
          };
        }

        return {
          success: false,
          message: response.msg,
        };
      },
    };

    const uploadButton = (
      <div className={styles['upload-btn']}>
        <Icon name="plus" size={25} />
        <div className={styles['upload-text']}></div>
      </div>
    );
    return (
      <div className="markdown-block">
        <h3>4</h3>
        <p> beforeUpload </p>
        <Upload {...props}>
          {this.state.fileList.length >= 3 ? null : uploadButton}
        </Upload>
      </div>
    );
  }
}

export default class UploadDemo extends Component {
  render() {
    return (
      <div className="markdown-block">
        <UploadDemo1 />
        <br /><br />
        <UploadDemo2 />
        <br /><br />
        <UploadDemo3 />
        <br /><br />
        <UploadDemo4 />
      </div>
    );
  }
}



