import { Component } from 'react';
import Alert from '../index';

export default class AlertDemo extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    const successProps = {
      type: 'success',
      message: '()',
    };
    const infoProps = {
      type: 'info',
      message: 'info',
      description: 'infoinfoinfoinfoinfoinfoinfoinfoinfoinfoinfo',
    };
    const errorProps = {
      type: 'error',
      message: '()',
      onClose() { console.log('error'); },
    };
    const warnProps = {
      type: 'warning',
      message: '()',
    };

    return (
      <div className="markdown-block">
        <h3></h3>
        <Alert {...successProps} />
        <h3></h3>
        <Alert {...errorProps} closable />
        <Alert type="warning" message="()" showIcon closable closeText="close me" />
        <h3></h3>
        <Alert {...infoProps} showIcon />
        <Alert {...successProps} showIcon />
        <Alert {...errorProps} showIcon />
        <Alert {...warnProps} showIcon />
        <h3></h3>
        <Alert {...infoProps} closable showIcon />
        <Alert type="error" message="error" description="()" />
      </div>
    );
  }
}



