import { Component } from 'react';
import Button from '../Button';

export default class ButtonDemo extends Component {
  render() {
    return (
      <div className="markdown-block">
        <h3></h3>
        <p></p>
        <table>
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td><Button type="primary"></Button></td>
              <td><Button type="secondary"></Button></td>
              <td><Button type="dashed"></Button></td>
              <td><Button type="danger"></Button></td>
            </tr>
          </tbody>
        </table>
        <h3></h3>
        <p></p>
        <p> size  large small  size</p>
        <Button size="large">()</Button>
        &nbsp;
        <Button>()</Button>
        &nbsp;
        <Button size="small">()</Button>
        <h3></h3>
        <p> disabled </p>
        <Button size="large" disabled></Button>
        &nbsp;
        <Button disabled></Button>
        &nbsp;
        <Button size="small" disabled></Button>
      </div>
    );
  }
}



